"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { Card, CardContent, CardHeader, CardTitle } from "~/components/ui/card";
import { Button } from "~/components/ui/button";
import { Input } from "~/components/ui/input";
import { Label } from "~/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "~/components/ui/select";
import { useToast } from "~/hooks/use-toast";
import { type Game, type Team } from "@prisma/client";

interface BookingData {
    id: number;
    teamId: number;
    gameId: number;
    code: string;
    expiresAt: string | null;
    team: {
        id: number;
        name: string;
    };
    game: {
        id: number;
        name: string;
    };
}

interface Props {
    params: {
        bookingId: string;
    };
}

export default function EditBookingPage({ params }: Props) {
    const [booking, setBooking] = useState<BookingData | null>(null);
    const [teams, setTeams] = useState<Team[]>([]);
    const [games, setGames] = useState<Game[]>([]);
    const [loading, setLoading] = useState(true);
    const [submitting, setSubmitting] = useState(false);
    const { toast } = useToast();
    const router = useRouter();
    const bookingId = parseInt(params.bookingId);

    useEffect(() => {
        async function fetchData() {
            try {
                const [bookingResponse, teamsResponse, gamesResponse] = await Promise.all([
                    fetch(`/api/bookings/${bookingId}`),
                    fetch('/api/teams'),
                    fetch('/api/games')
                ]);

                if (!bookingResponse.ok) throw new Error('Failed to fetch booking');
                if (!teamsResponse.ok) throw new Error('Failed to fetch teams');
                if (!gamesResponse.ok) throw new Error('Failed to fetch games');

                const bookingData = await bookingResponse.json();
                const teamsData = await teamsResponse.json();
                const gamesData = await gamesResponse.json();

                setBooking(bookingData.booking);
                setTeams(teamsData.teams);
                setGames(gamesData.games);
            } catch (error) {
                console.error('Error fetching data:', error);
                toast({
                    variant: "destructive",
                    title: "Error",
                    description: "Failed to load booking data",
                });
                router.push('/admin/bookings');
            } finally {
                setLoading(false);
            }
        }

        if (isNaN(bookingId)) {
            toast({
                variant: "destructive",
                title: "Error",
                description: "Invalid booking ID",
            });
            router.push('/admin/bookings');
            return;
        }

        void fetchData();
    }, [bookingId, router, toast]);

    async function onSubmit(e: React.FormEvent<HTMLFormElement>) {
        e.preventDefault();
        setSubmitting(true);

        try {
            const formData = new FormData(e.currentTarget);
            const teamId = parseInt(formData.get("teamId") as string);
            const gameId = parseInt(formData.get("gameId") as string);
            const expiresAt = formData.get("expiresAt") as string;

            const response = await fetch(`/api/bookings/${bookingId}`, {
                method: "PATCH",
                headers: {
                    "Content-Type": "application/json",
                },
                body: JSON.stringify({
                    teamId,
                    gameId,
                    expiresAt: expiresAt || null,
                }),
            });

            if (!response.ok) {
                throw new Error("Failed to update booking");
            }

            toast({
                title: "Success",
                description: "Booking updated successfully",
            });
            router.push("/admin/bookings");
            router.refresh();
        } catch (error) {
            console.error("Error updating booking:", error);
            toast({
                variant: "destructive",
                title: "Error",
                description: "Failed to update booking",
            });
        } finally {
            setSubmitting(false);
        }
    }

    if (loading || !booking) {
        return <div>Loading...</div>;
    }

    return (
        <div className="max-w-2xl">
            <div className="mb-8">
                <h1 className="text-3xl font-bold tracking-tight">Edit Booking</h1>
                <p className="text-slate-400">Update booking details for code: <span className="font-mono">{booking.code}</span></p>
            </div>

            <Card className="border-slate-800 bg-slate-900/50">
                <CardHeader>
                    <CardTitle>Booking Details</CardTitle>
                </CardHeader>
                <CardContent>
                    <form onSubmit={onSubmit} className="space-y-6">
                        <div className="space-y-2">
                            <Label htmlFor="teamId">Team</Label>
                            <Select name="teamId" defaultValue={booking.teamId.toString()} required>
                                <SelectTrigger className="bg-slate-950">
                                    <SelectValue placeholder="Select a team" />
                                </SelectTrigger>
                                <SelectContent>
                                    {teams.map((team) => (
                                        <SelectItem key={team.id} value={team.id.toString()}>
                                            {team.name}
                                        </SelectItem>
                                    ))}
                                </SelectContent>
                            </Select>
                        </div>

                        <div className="space-y-2">
                            <Label htmlFor="gameId">Game</Label>
                            <Select name="gameId" defaultValue={booking.gameId.toString()} required>
                                <SelectTrigger className="bg-slate-950">
                                    <SelectValue placeholder="Select a game" />
                                </SelectTrigger>
                                <SelectContent>
                                    {games.map((game) => (
                                        <SelectItem key={game.id} value={game.id.toString()}>
                                            {game.name}
                                        </SelectItem>
                                    ))}
                                </SelectContent>
                            </Select>
                        </div>

                        <div className="space-y-2">
                            <Label htmlFor="expiresAt">
                                Expires At
                                <span className="ml-1 text-sm text-slate-400">
                                    (optional)
                                </span>
                            </Label>
                            <Input
                                type="datetime-local"
                                name="expiresAt"
                                defaultValue={booking.expiresAt?.slice(0, 16) || ""}
                                className="bg-slate-950"
                            />
                        </div>

                        <div className="flex justify-end gap-4">
                            <Button
                                type="button"
                                variant="outline"
                                onClick={() => router.back()}
                                disabled={submitting}
                            >
                                Cancel
                            </Button>
                            <Button type="submit" disabled={submitting}>
                                {submitting ? "Saving..." : "Save Changes"}
                            </Button>
                        </div>
                    </form>
                </CardContent>
            </Card>
        </div>
    );
} 